import React from "react";
import Downshift from "downshift";
import Router from "next/router";
import { ApolloConsumer } from "react-apollo";
import gql from "graphql-tag";
import debounce from "lodash.debounce";
import { DropDown, DropDownItem, SearchStyles } from "./styles/DropDown";

const SEARCH_ITEMS_QUERY = gql`
  query SEARCH_ITEMS_QUERY($searchTerm: String!) {
    # we are doing OR because if we just put in two params in our where, both would have contain the term
    # also, title_contains and description_contains are part of our generated filters in prisma
    items(
      where: {
        OR: [
          { title_contains: $searchTerm }
          { description_contains: $searchTerm }
        ]
      }
    ) {
      id
      image
      title
    }
  }
`;

function routeToItem(item) {
  Router.push({
    pathname: "/item",
    query: {
      id: item.id,
    },
  });
}

class AutoComplete extends React.Component {
  state = {
    items: [],
    loading: false,
  };
  onChange = debounce(async (e, client) => {
    console.log("loading...");
    //turn loading on
    this.setState({ loading: true });
    // Manually query the apollo client
    const res = await client.query({
      query: SEARCH_ITEMS_QUERY,
      variables: { searchTerm: e.target.value },
    });
    this.setState({
      items: res.data.items,
      loading: false,
    });
  }, 350);
  render() {
    return (
      <SearchStyles>
        <Downshift
          onChange={routeToItem}
          itemToString={(item) => (item === null ? "" : item.title)}
        >
          {({
            getInputProps,
            getItemProps,
            isOpen,
            inputValue,
            highlightedIndex,
          }) => (
            <div>
              {/* Instead of wrapping out input in a <Query/> (which would query every time we render our search compinent,
                we can wrap it in ApolloConsumer to expose our client to the input 
                )*/}
              <ApolloConsumer>
                {(client) => (
                  <input
                    //getIntputProps it will let us fill in the text from the item we select to the input
                    {...getInputProps({
                      type: "search",
                      placeholder: "Search for an item",
                      id: "search",
                      className: this.state.loading ? "laoding" : "",
                      onChange: (e) => {
                        e.persist();
                        this.onChange(e, client);
                      },
                    })}
                  />
                )}
              </ApolloConsumer>
              {/* Dropdown library is what gives us the isOpen, which allows us to click away from the search bar or press esc */}
              {isOpen && (
                <DropDown>
                  {this.state.items.map((item, index) => (
                    <DropDownItem
                      {...getItemProps({ item })}
                      key={item.id}
                      //highlightedIndex comes from Dropdown, but highlighted is a prop we make that we can pass down
                      //if you look at the styles in DropdownItem, you'll this this only returns a boolean
                      highlighted={index === highlightedIndex}
                    >
                      <img width="50" src={item.image} alt={item.title} />
                      {item.title}
                    </DropDownItem>
                  ))}
                </DropDown>
              )}
            </div>
          )}
          {!this.items.state.length && !this.state.loading && (
            <DropDownItem> Nothing found for {inputValue}</DropDownItem>
          )}
        </Downshift>
      </SearchStyles>
    );
  }
}

export default AutoComplete;
